<template>
    <div class="contains">

        <h1>Priradiť používateľovi kartu</h1>
        <div class="field">
            <label class="label">Voľné karty</label>
            <div class="select">
                <select v-model="user.card">
                    <option value="">Vyberte kartu</option>
                    <option v-for="card in freeCards" :key="card.id" :value="card.id">{{ card.card }}</option>
                </select>
            </div>
        </div>

        <button class="button is-primary" @click="addFreeCard(user)">Pridať</button>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex';

    export default {
        name: "AddCard",
        data() {
            return {
                user: {
                    id: this.$route.params.id,
                    card: ''
                }
            }
        },
        methods: {
            ...mapActions({
                getFreeCards: 'lockLogs/getFreeCards',
                addFreeCard: 'lockLogs/addFreeCard',
            }),
        },
        computed: {
            ...mapGetters({
                freeCards: 'lockLogs/freeCards'
            })
        },
        async created() {
            await this.getFreeCards()
        },
        watch:{
          async freeCards (){}
        }
    }
</script>

<style scoped>
    h1 {
        font-size: 24px;
    }

    .contains {
        width: 600px;
        background-color: #FFFFFF;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        border-radius: 20px;
        padding: 30px;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        margin-top:20px;
    }

    .field {
        text-align: left;
    }

    .select{
        width:100%;
    }
    select{
        width:100%;
    }
    .hover:hover {
        background-color: #00ccb2;
        color: white;
    }
</style>