<template>
    <div class="contains">
        <div class="up">
            <h1>Používatelia a karty</h1>
            <button class="button is-primary"><router-link to="/new-user">Pridať nového používateľa</router-link></button>
        </div>
        <div style="overflow-x:auto;">
        <table class="table">
            <thead>
            <tr>
                <th>Číslo používateľa</th>
                <th>Používateľ</th>
                <th>Email</th>
                <th>Číslo karty</th>
                <th>Registrácia</th>
                <th>Editácia</th>
            </tr>
            </thead>
            <tbody>
            <tr class="hover" v-for="user in userCards" :key="user.id">
                <th>{{ user.id }}</th>
                <td>{{ user.name }}</td>
                <td>{{ user.email }}</td>
                <td v-if="user.card">{{ user.card }}</td>
                <td v-else>Nepridaná</td>
                <td v-if="user.registration">{{ changeDate(user.registration) }}</td>
                <td v-else>--</td>
                <td>
                    <button v-if="user.registration" class="button is-danger" @click="deleteCard(user.id)">Odstrániť kartu</button>
                    <button v-else class="button is-primary"><router-link v-bind:to="'add-card/'+user.id" >Priradiť kartu</router-link></button>
                </td>
            </tr>
            </tbody>
        </table>
        </div>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex';
    import moment from 'moment';

    export default {
        name: "Users",
        methods:{
            ...mapActions({
                fetchUserCards: 'lockLogs/getUserCards',
                deleteCard: 'lockLogs/deleteCard'
            }),
            changeDate:function (date) {
                if (date) {
                    return moment(String(date)).format('hh:mm:ss DD.MM.YYYY ')
                }
            },

        },
        computed: {
            ...mapGetters({
                userCards: 'lockLogs/userCards',
            })
        },
        async created(){
            await this.fetchUserCards()
        },
        watch: {
            async userCards(){}
        }
    }
</script>

<style scoped>
    h1{
        font-size: 24px;
    }
    .contains{
        width:100%;
        background-color: #FFFFFF;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        border-radius: 20px;
        padding:30px;

    }
    a{
        color:white;
    }
    .up{
        display:flex;
        justify-content: space-between;
    }
    table{
        margin-top:20px;
        width:100%;
    }
    .hover:hover{
        background-color: #00ccb2;
        color:white;
    }
    @media screen and (max-width: 1024px) {
        .up{
            flex-flow: column;
        }
    }
</style>