import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Cards from '../views/Cards.vue'
import NewUser from "../views/NewUser";
import AddCard from "../views/AddCard";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/users-cards',
    name: 'Cards',
    component: Cards
  },
  {
    path: '/new-user',
    name: 'NewUser',
    component: NewUser
  },
  {
    path: '/add-card/:id',
    name: 'AddCard',
    component: AddCard
  },

]

const router = new VueRouter({
  mode:'history',
  routes: routes
})

export default router
