<template>
  <div class="contains">
    <h1>Nedávne otvárania</h1>
    <table class="table">
      <thead>
      <tr>
        <th>Číslo vstupu</th>
        <th>Používateľ</th>
        <th>Dátum a čas</th>
      </tr>
      </thead>
      <tbody>
      <tr class="hover" v-for="log in fewLogs" :key="log.id">
        <th>{{ log.id }}</th>
        <td>{{ log.name }}
        </td>
        <td>{{ changeDate(log.datetime) }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';
  import moment from 'moment'

export default {
  name: 'FewLogs',
  methods:{
    ...mapActions({
      fetchFewLogs: 'lockLogs/getFewLogs'
    }),
    changeDate:function (date) {
      if (date) {
        return moment(String(date)).format('hh:mm:ss DD.MM.YYYY ')
      }
    },
    getLogs: function(){
      setInterval(() => {
        this.fetchFewLogs()
      }, 3000);
    }
  },
  computed: {
    ...mapGetters({
      fewLogs: 'lockLogs/fewLogs'
    })
  },
  async created(){
    this.fetchFewLogs()
    await this.getLogs()
  },
  watch: {
    async fewLogs(){}
  }
}
</script>

