<template>
    <div class="contains">
        <div style="display:flex; justify-content: space-between;">
            <h1>Používatelia a karty</h1>
        </div>

        <table class="table">
            <thead>
            <tr>
                <th>Číslo používateľa</th>
                <th>Používateľ</th>
                <th>Číslo karty</th>
                <th>Registrácia</th>
            </tr>
            </thead>
            <tbody>
            <tr class="hover" v-for="user in userCards" :key="user.id">
                <th>{{ user.id }}</th>
                <td>{{ user.name }}</td>
                <td v-if="user.card">{{ user.card }}</td>
                <td v-else>Nepridaná</td>
                <td v-if="user.registration">{{ changeDate(user.registration) }}</td>
                <td v-else>--</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex';
    import moment from 'moment';

    export default {
        name: "UserCards",
        methods:{
            ...mapActions({
                fetchUserCards: 'lockLogs/getUserCards'
            }),
            changeDate:function (date) {
                if (date) {
                    return moment(String(date)).format('hh:mm:ss DD.MM.YYYY ')
                }
            },

        },
        computed: {
            ...mapGetters({
                userCards: 'lockLogs/userCards'
            })
        },
        async created(){
            await this.fetchUserCards()
        },
        watch: {
            async userCards(){}
        }
    }
</script>

<style scoped>
    h1{
        font-size: 24px;
    }
    .contains{
        width:100%;
        background-color: #FFFFFF;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        border-radius: 20px;
        padding:30px;
    }
    table{
        margin-top:20px;
        width:100%;
    }
    .hover:hover{
        background-color: #00ccb2;
        color:white;
    }
</style>