import axios from "axios";

const state = {
    fewLogs: {},
    userCards: {},
    freeCards:{}
};
const getters = {
    fewLogs: (state) => state.fewLogs,
    userCards: (state) => state.userCards,
    freeCards: (state) => state.freeCards,
};
const actions = {
    async getFewLogs({commit}) {
        var url = 'http://www.wirelesslock.wz.sk/get_few_logs.php';
        var formData = new FormData();

        formData.append('KEY', '1qN2659PM2ChDGL74zsbX1nQPpCUGDkY');
        axios({
            method: 'post',
            url: url,
            data: formData,
            body: formData
        }).then(function (response) {
            var text = response.data
            var data = text.toString().substring(text.toString().indexOf('-->[{') + 3, text.toString().length)
            commit('setFewLogs', JSON.parse(data));
        })
            .catch(function (response) {
                //handle error
                console.log(response);
            });
    },
    async getUserCards({commit}) {
        var url = 'http://www.wirelesslock.wz.sk/get_user_card.php';
        var formData = new FormData();

        formData.append('KEY', '1qN2659PM2ChDGL74zsbX1nQPpCUGDkY');
        axios({
            method: 'post',
            url: url,
            data: formData,
            body: formData
        }).then(function (response) {
            var text = response.data
            var data = text.toString().substring(text.toString().indexOf('-->[{') + 3, text.toString().length)
            commit('setUserCards', JSON.parse(data));
        })
            .catch(function (response) {
                //handle error
                console.log(response);
            });
    },
    async getFreeCards({commit}) {
        var url = 'http://www.wirelesslock.wz.sk/get_free_cards.php';
        var formData = new FormData();

        formData.append('KEY', '1qN2659PM2ChDGL74zsbX1nQPpCUGDkY');
        axios({
            method: 'post',
            url: url,
            data: formData,
            body: formData
        }).then(function (response) {
            var text = response.data
            var data = text.toString().substring(text.toString().indexOf('-->[{') + 3, text.toString().length)
            commit('setFreeCards', JSON.parse(data));
        })
            .catch(function (response) {
                //handle error
                console.log(response);
            });
    },
    async deleteCard({commit}, data) {
        var url = 'http://www.wirelesslock.wz.sk/delete_card.php';
        var formData = new FormData();

        formData.append('KEY', '1qN2659PM2ChDGL74zsbX1nQPpCUGDkY');
        formData.append('ID', data);
        axios({
            method: 'post',
            url: url,
            data: formData,
            body: formData
        }).then(function (response) {
            var text = response.data
            var data = text.toString().substring(text.toString().indexOf('-->[{') + 3, text.toString().length)
            window.location.replace("users-cards");
        })
            .catch(function (response) {
                //handle error
                console.log(response);
            });
    },
    async addNewUser({commit}, data) {
        var url = 'http://www.wirelesslock.wz.sk/add_new_user.php';
        var formData = new FormData();

        formData.append('KEY', '1qN2659PM2ChDGL74zsbX1nQPpCUGDkY');
        formData.append('name', data.name);
        formData.append('email', data.email);
        axios({
            method: 'post',
            url: url,
            data: formData,
            body: formData
        }).then(function (response) {
            var text = response.data
            var data = text.toString().substring(text.toString().indexOf('-->[{') + 3, text.toString().length)

                window.location.replace("users-cards");

        })
            .catch(function (response) {
                //handle error
                console.log(response);
            });
    },
    async addFreeCard({commit}, data) {
        var url = 'http://www.wirelesslock.wz.sk/add_free_card.php';
        var formData = new FormData();

        formData.append('KEY', '1qN2659PM2ChDGL74zsbX1nQPpCUGDkY');
        formData.append('id', data.id);
        formData.append('card', data.card);
        axios({
            method: 'post',
            url: url,
            data: formData,
            body: formData
        }).then(function (response) {
            var text = response.data
            var data = text.toString().substring(text.toString().indexOf('-->[{') + 3, text.toString().length)

            window.location.replace("/users-cards");

        })
            .catch(function (response) {
                //handle error
                console.log(response);
            });
    },
};

const mutations = {
    setFewLogs: (state, fewLogs) => (state.fewLogs = fewLogs),
    setUserCards: (state, userCards) => (state.userCards = userCards),
    setFreeCards: (state, freeCards) => (state.freeCards = freeCards),
};
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}