<template>
    <div class="contains">

        <h1>Pridať používateľa</h1>
        <div class="field">
            <label class="label">Celé meno</label>
            <div class="control has-icons-left has-icons-right">
                <input class="input" type="text" placeholder="Celé meno" v-model="user.name">
                <span class="icon is-small is-left">
                    <i class="fas fa-user"></i>
                </span>
            </div>
        </div>
        <div class="field">
            <label class="label">Email</label>
            <div class="control has-icons-left has-icons-right">
                <input class="input" type="email" placeholder="Email " v-model="user.email">
                <span class="icon is-small is-left">
                    <i class="fas fa-envelope"></i>
                 </span>
            </div>
        </div>
        <button class="button is-primary" @click="addNewUser(user)">Pridať</button>
    </div>
</template>


<script>
    import {mapGetters, mapActions} from 'vuex';

    export default {
        name: "NewUser",
        data() {
            return {
                user: {
                    name: '',
                    email: ''
                }
            }
        },
        methods: {
            ...mapActions({
                addNewUser: 'lockLogs/addNewUser'
            }),
        },
        computed: {
            ...mapGetters({
                userCards: 'lockLogs/userCards'
            })
        }
    }
</script>

<style scoped>
    h1 {
        font-size: 24px;
    }

    .contains {
        width: 600px;
        background-color: #FFFFFF;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        border-radius: 20px;
        padding: 30px;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        margin-top:20px;
    }

    .field {
        text-align: left;
    }

    table {
        margin-top: 20px;
        width: 100%;
    }

    .hover:hover {
        background-color: #00ccb2;
        color: white;
    }
</style>